<template>
  <div>
    <div class="info">
      <div class="fg"></div>
    </div>
    <div class="userinfo">
      <div class="head_div"><img :src="userinfo.headimg"></div>
      <p v-html="userinfo.nickname"></p>
      <p><i></i><span v-html="userinfo.city"></span>|<span v-html="setexp(userinfo.exp)"></span></p>
      <!--擅长-->
      <div class="sc">
        <span style="float: left;">擅长领域：</span>
        <ul style="float: right;width: 313px;padding-top: 10px">
          <li v-for="item in userinfo.filed" v-html="item"></li>
        </ul>
      </div>
      <div v-show="userinfo.worktime.length>0" class="worktime" style="overflow: hidden;"><span>可工作时间：</span>
        <ul style="float: right;width: 313px;color: #313131;line-height: 28px;margin-top: 6px;">
          <li v-for="item in userinfo.worktime" v-html="formatWorktime(item)"></li>
        </ul>
      </div>
      <!--项目经历-->
      <div v-show="userinfo.project_text.length>0" style="overflow: hidden;margin-top: 20px;">
        <span style="float: left;">项目经历：</span>
        <ul id="project_text"
            style="float: right;width: 313px;line-height: 20px;margin-top: 10px;height: 62px;overflow: hidden;">
          <li v-for="item in userinfo.project_text">
            <p style="font-weight: 600;" v-html="item.title"></p>
            <p style="margin-bottom: 40px;" v-html="item.desc"></p>
          </li>
        </ul>
      </div>
      <div v-show="userinfo.project_text.length>0" @click="showmore" class="more"><i
        class="iconfontyyy">&#xe8f8;</i><span>展开</span>
      </div>
    </div>
    <div class="main">
      <div style="height: 80px;background: #fff;border-top: 1px solid #dedede;">
        <ul id="category" class="category">
          <li class="active"><a>全部</a></li>
          <li><a>PPT定制</a></li>
          <li><a>UI设计</a></li>
          <li><a>企业画册</a></li>
          <li><a>海报展板</a></li>
          <li><a>LOGO</a></li>
          <li><a>企业形象设计VI</a></li>
        </ul>
      </div>
      <div class="content">

        <!--显示的品类-->
        <ul style="overflow: hidden">
          <li v-for="itme in albums">
            <!--todo 此处路由均改成在新窗口打开-->
            <!--<router-link tag="img" :to="{name:'project',params:{'id':itme.work_id}}" class="img"
                         :src="itme.cover"></router-link>-->
            <img :src="itme.cover" @click="jump({'name':'project','id':itme.work_id})" class="img">
            <p>
              <!--<router-link tag="span" style="padding-left: 0;cursor: pointer"
                           :to="{name:'project',params:{'id':itme.work_id}}" v-html="itme.title"></router-link>-->
              <span
                style="padding-left: 0px;cursor: pointer;white-space: nowrap;display: inline-block; width: 100%;overflow: hidden;text-overflow: ellipsis;"
                :title="itme.title" v-html="itme.title"
                @click="jump({'name':'project','id':itme.work_id})"></span>
            </p>
            <p v-html="myfilter(itme.up_time)"></p>
            <p>
              <!-- <router-link tag="img" :to="{name:'wechat',params:{'id':itme.user_id}}" :src="itme.headimg"></router-link>
               <router-link tag="span" :to="{name:'wechat',params:{'id':itme.user_id}}"
                            v-html="itme.username"></router-link>-->
              <img :src="itme.headimg" @click="jump({'name':'wechat','id':itme.user_id})">
              <span v-html="itme.username" @click="jump({'name':'wechat','id':itme.user_id})"></span>
              <span
                style='font-size: 14px;float: right;margin-right: 10px;'
                v-html="itme.category"></span></p>
          </li>
        </ul>
        <pagination :pageInfo="pageinfo" @change="pagechange"></pagination>
      </div>
    </div>
  </div>
</template>
<script>
  import pagination from './Pagination'

  function getDateTimeStamp(dateStr) {
    return Date.parse(dateStr.replace(/-/gi, "/"));
  }

  function getDateDiff(dateTimeStamp) {
    var minute = 1000 * 60;
    var hour = minute * 60;
    var day = hour * 24;
    var month = day * 30;
    var now = new Date().getTime();
    var diffValue = now - dateTimeStamp;
    var monthC = diffValue / month;
    var weekC = diffValue / (7 * day);
    var dayC = diffValue / day;
    var hourC = diffValue / hour;
    var minC = diffValue / minute;
    var result = "刚刚";
    if (monthC >= 1) {
      result = parseInt(monthC) + "个月前";
    } else if (weekC >= 1) {
      result = parseInt(weekC) + "周前";
    } else if (dayC >= 1) {
      result = parseInt(dayC) + "天前";
    } else if (hourC >= 1) {
      result = parseInt(hourC) + "小时前";
    } else if (minC >= 1) {
      result = parseInt(minC) + "分钟前";
    }
    return result;
  }


  export default{
    data(){
      return {
        /*name: "猴小瘦",
         address: "浙江·杭州",
         exp: "5年工作经验",
         worktime: "周一到周五白天",*/
        userinfo: {
          project_text: [],
          worktime: []
        },
        albums: [],
        userid: this.$route.params.id,
        pageinfo: {
          total: 0,     // 记录总条数   默认空，如果小于pageNum则组件不显示   类型Number
          pagenum: 20,    // 每页显示条数,默认10                              类型Number
          current: 1,     // 当前页数，   默认为1                             类型Number
          pagegroup: 5,     // 分页条数     默认为5，需传入奇数                  类型Number
          skin: '#d01667',
        },
        categroy: '-1',
        more: false,
      }
    },
    created(){
      console.log(this.userid);
      this.getuserinfo(this.userid);
      this.getuserworks(this.userid, this.categroy, this.pageinfo.current, 1);
    },
    mounted(){
      this.chooseCategory();
    },
    components: {
      pagination
    },
    methods: {
      setexp(exp){
        var y = new Date().getFullYear();
        if (exp >= y) {
          return "学生"
        } else {
          return (y - exp) + "年工作经验";
        }
      },
      /*splitCity(value){//分割所在地
       var sheng = "";
       var shi = "";
       var a = value.split("省");
       if (a.length > 1) {
       sheng = a[0];
       var b = a[1].split("市");
       shi = b[0];
       } else {
       var zzq = a[0];
       var c = zzq.split("自治区");
       if (c.length > 1) {
       sheng = c[0];
       } else {
       var d = zzq.split("特别行政区");
       if(d.length>1){
       sheng = d[0];
       }
       }
       var b = a[0].split("市");
       shi = b[0];
       }
       if (sheng == "") {
       return shi;
       } else {
       return sheng + "&nbsp;" + shi;
       }
       }*/
      showmore(){
        this.more = !this.more;
        if (this.more) {
          $("#project_text").css("height", "auto");
          $(".userinfo .more i").eq(0).html("&#xe8fa;");
          $(".userinfo .more span").eq(0).html("收起");
        } else {
          $("#project_text").css("height", "62px");
          $(".userinfo .more i").eq(0).html("&#xe8f8;");
          $(".userinfo .more span").eq(0).html("展开");
          window.scrollTo(0, 600);
        }
      },
      jump(data){
        var id = data.id;
        if (data.name === "project") {
          window.open("http://houxiaopang.com/workdetail/#/album/" + id);
        } else if (data.name === "wechat") {
          window.open("http://houxiaopang.com/workdetail/#/user/" + id);
        }
      },
      pagechange(current){
        this.getuserworks(this.userid, this.categroy, current);
      },
      chooseCategory(){
        var that = this;
        var li = document.getElementById("category").children;
        for (var i = 0, size = li.length; i < size; i++) {
          li[i].index = i;
          li[i].onclick = function () {
            $("#category").find("li").each(function () {
              $(this).removeClass("active");
            });
            $(this).addClass("active");

            if (this.index == 0) {
              this.index = -1;
            }
            that.pageinfo.current = 1;
            that.categroy = this.index;
            that.getuserworks(that.userid, that.categroy, that.pageinfo.current);
          }
        }
      },
      getuserinfo(id){
        var that = this;
        $.ajax({
          type: "get",
          url: "http://www.houxiaopang.com/api/v1.1/resumeinfo",
          data: {
            'designer_id': id,
          },
          success(data){
            if (data.code === 0) {
              if (data.data.worktime == "") {
                data.data.worktime = []
              }
              that.userinfo = data.data;
            } else {
              alert("网络拥挤，请稍后再试···");
            }
          },
          error(){
            alert("网络拥挤，请稍后再试···");
          }
        });

      },
      getuserworks(id, c, page, flag){
        var that = this;
        $.ajax({
          type: "get",
          url: "http://www.houxiaopang.com/api/v1.1/allwork",
          data: {
            'category': c,
            'designer_id': id,
            'page': page
          },
          success(data){
            if (data.code === 0) {
              if (!flag) {
                window.scrollTo(0, document.getElementById("category").offsetTop);
              }
              that.albums = [];
              var json = data.data.album;
              that.pageinfo.total = data.data.total;
              for (var i = 0, size = json.length; i < size; i++) {
                var type = json[i].category;
                if (type === 1) {
                  type = "PPT";
                } else if (type === 2) {
                  type = "UI";
                } else if (type === 3) {
                  type = "文本画册";
                } else if (type === 4) {
                  type = "海报展板";
                } else if (type === 5) {
                  type = "LOGO";
                } else if (type === 6) {
                  type = "企业形象设计（VI）";
                } else if (type === 0) {
                  type = "测试品类";
                }
                json[i].category = type;
                that.albums.push(json[i]);
              }
            } else {
              alert("网络拥挤，请稍后再试···");
            }
          },
          error(){
            alert("网络拥挤，请稍后再试···");
          }
        });
      },
      formatWorktime(value){
        var worktime = "";
        if (value == "1") {
          worktime = "周一至周五白天";
        } else if (value == "2") {
          worktime = "周一至周五晚间";
        } else if (value == "3") {
          worktime = "周末";
        } else if (value == "4") {
          worktime = "可全职服务";
        } else if (value == "5") {
          worktime = "另议";
        }
        return worktime;
      },
      myfilter(value){
        return getDateDiff(getDateTimeStamp(value));
      },

    }
  }
</script>

<style scoped>
  .info {
    background: url('http://image.houxiaopang.com/workdetail/bg.png') no-repeat;
    background-size: cover;
    height: 340px;
  }

  .fg {
    background: rgba(0, 0, 0, 0.6);
    width: 100%;
    left: 0;
    top: 0;
    height: 100%;
  }

  ul {
    list-style: none;
  }

  .userinfo {
    width: 600px;
    margin: 0 auto;
    z-index: 10;
    box-sizing: border-box;
    color: #727272;
    line-height: 40px;
    padding: 15px 60px 50px;
  }

  .userinfo > .head_div {
    margin: -70px auto 0 auto;
    width: 110px;
    height: 110px;
    background: #fff;
    border-radius: 50%;
  }

  .userinfo > .head_div > img {
    display: inline-block;
    width: 100px;
    height: 100px;
    margin-left: 5px;
    margin-top: 5px;
    border-radius: 50%;

  }

  .userinfo > p:nth-of-type(1) {
    font-size: 30px;
    text-align: center;
    height: 50px;
    line-height: 50px;
    color: #313131;

  }

  .userinfo > p:nth-of-type(2) {
    text-align: center;
    color: #999;
    height: 30px;
    line-height: 30px;
    font-size: 16px;
    position: relative;
  }

  .userinfo > p:nth-of-type(2) > span:nth-child(2) {
    padding-right: 15px;
    padding-left: 20px;
  }

  .userinfo > p:nth-of-type(2) > span:nth-child(3) {
    padding-left: 15px;
  }

  .userinfo > p > i {
    background: url('http://image.houxiaopang.com/workdetail/dingwei.png') no-repeat;
    display: inline-block;
    width: 12px;
    height: 17px;
    background-size: cover;
    position: absolute;
    top: 6px;
  }

  .main {
    background: #F5F5F5;
  }

  .sc {
    overflow: hidden;
    margin-top: 20px;
  }

  .userinfo > .sc li {
    float: left;
    padding: 0 25px;
    border-radius: 25px;
    text-align: center;
    height: 30px;
    line-height: 28px;
    margin-right: 20px;
    margin-bottom: 10px;
    background: #eee;
    color: #828282;
  }

  .userinfo > .more {
    width: 60px;
    margin: 0 auto;
    color: #c0bebe;
    cursor: pointer;
  }

  .userinfo > .more:hover {
    color: #d01667;
  }

  .userinfo > .more > span {
    padding-left: 5px;
    font-size: 14px;
  }

  .content {
    box-sizing: border-box;
    width: 1100px;
    margin: 20px auto 0;
    height: 100%;
  }

  .content > ul {
    overflow: hidden;
    box-sizing: border-box;
    list-style: none;
  }

  .content > ul > li {
    width: 260px;
    height: 295px;
    float: left;
    box-sizing: border-box;
    margin-right: 19px;
    margin-bottom: 25px;
    overflow: hidden;
    background: #fff;
  }

  .content > ul > li:nth-child(4n) {
    margin-right: 0;
  }

  .content > ul > li:hover {
    box-shadow: 3px 3px 5px 0 #d8d5d5;
  }

  .content > ul > li .img {
    width: 240px;
    height: 180px;
    cursor: pointer;
    margin: 10px 10px 0;
  }

  .content > ul > li > p {
    color: #313131;
    box-sizing: border-box;
    padding: 0 10px;
  }

  .content > ul > li > p:nth-of-type(1) {
    font-size: 14px;
    color: #333;
    margin-top: 1px;
  }

  .content > ul > li > p:nth-of-type(2) {
    font-size: 12px;
