  }

  .content > ul > li > p > span:nth-of-type(1) {
    cursor: pointer;
  }

  .content > ul > li > p > span {
    vertical-align: top;
    font-size: 14px;
  }

  .category {
    padding-top: 25px;
    width: 1000px;
    margin: 0 auto;
  }

  .category li:hover {
    border-bottom: 2px solid #d01667;
    cursor: pointer;
  }

  .category li {
    display: inline-block;
    margin-right: 30px;
    font-size: 16px;
    padding: 4px 5px;
  }

  .category li a {
